.loading {
    padding-top: 18px;
    padding-left: 5px;
    padding-bottom: 18px;
    background: darkgray ;
    border-radius: 10px;
    margin-bottom: 7px;
}

.loading-image {
    width: 20px;
    height: 20px;
    animation: loading-rotate 2s infinite linear;
}

@keyframes loading-rotate {
    100% {
        transform: rotate(360deg);
    }
}
