@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@300;400&display=swap');

body {
    margin: 0;
}

.postPage-buttons {
    text-align: center;
    font-family: 'Kanit', sans-serif;
    font-size: 10px;
}

.postPage-buttons-refresh {
    width: 30px;
    height: 30px;
    border: unset;
    background-color: transparent;
    cursor: pointer;
}

.postPage-buttons-refresh-close{
    width: 30px;
    height: 30px;
    border: unset;
    background-color: transparent;
    cursor: pointer;
    transition: 0.5s linear;
    transform: rotate(220deg);
}


.postPage-buttons-refresh-close path{
    fill: white;
}

.postPage-buttons-refresh:hover path{
    fill: chartreuse;
}

.postPage-buttons-back {
    width: 30px;
    height: 30px;
    border: unset;
    background-color: transparent;
    cursor: pointer;
}

.postPage-buttons-back:hover path{
    fill: chartreuse;
}

.postPage-title {
    font-family: 'Kanit', sans-serif;
    font-size: 25px;
    color: white;
}

.postPage-info {
    font-family: 'Kanit', sans-serif;
    font-size: 15px;
}

.postPage-link {
    font-family: 'Kanit', sans-serif;
    text-decoration: none;
    color: rgba(43, 43, 43, 0.87);
    font-size: 13px;
}

.postPage-link:hover {
    color: white;
}

.postPage-container {
    padding: 18px 10px;
    background: darkgray ;
    border-radius: 10px;
}

.postPage-comments {
    margin-top: 20px;
    font-family: 'Kanit', sans-serif;
    text-decoration: none;
    color: rgba(43, 43, 43, 0.87);
    font-size: 13px;
}

.postPage-comments ul {
    padding-inline-start: 0;
    margin-block-start: 6px;
}
