@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@300;400&display=swap');

.main {
    margin: 30px 70px 40px;
    padding: 20px 50px;
    background: rgba(43, 43, 43, 0.38);
    border-radius: 10px;

}


.main-header {
    font-family: 'Kanit', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-head {
    margin-top: 14px;
    margin-right: 3px;
}

.refresh-button {
    width: 30px;
    height: 30px;
    border: unset;
    background-color: transparent;
    cursor: pointer;
}

.refresh-button:hover path{
    fill: chartreuse;
}

.posts-zero {
    font-family: 'Kanit', sans-serif;
    font-size: 20px;
    text-align: center;
    font-weight: lighter;
    padding-inline-start: 0;
}

.main-up-btn {
    position: fixed;
    right: 18px;
    bottom: 20px;
    cursor: pointer;
    background-color: transparent;
    width: 35px;
    border: unset;
}

.main-up-btn:hover svg path{
    fill: #3ab81d;
}




