
.post {
    font-family: 'Kanit', sans-serif;
    padding-top: 18px;
    padding-left: 5px;
    padding-bottom: 18px;
    background: darkgray ;
    border-radius: 10px;
    margin-bottom: 7px;
}

.post-link {
   text-decoration: none;
}

.post:hover {
    background-color: rgba(127, 255, 0, 0.4);
}

.post-star {
    height: 10px;
    width: 10px;
    margin-right: 3px;
}

.post-info {
    display: flex;
    align-items: center;
    color: black;
    font-size: 13px;
}

.post-heading {
    margin: 0 0 5px;
    color: #ffffff;
    text-shadow: 1px 1px 2px black;
}


