.rootComments {
    padding: 7px 10px 7px 10px;
    background: rgba(255, 255, 255, 0.62);
    border-radius: 4px;
    margin-bottom: 10px;
}

.rootComments-date {
    margin: 0;
    font-family: 'Kanit', sans-serif;
    font-size: 13px;
    color: black;
}

.rootComments-text {
    margin: 0;
    font-size: 12px;
    color: midnightblue;
}

.rootComments-text p {
    margin-top: 0;
    margin-bottom: 0;
}

.rootComments-btn {
    font-size: 12px;
    margin-top: 4px;
}

.rootComments-btn:hover {
    cursor: pointer;
    color: #3ab81d;
}

.comment {
    margin: 0;
    font-size: 12px;
    color: #3ab81d;
}

.comment-text {
    margin-bottom: 2px;
    font-size: 12px;
    color: rgba(43, 43, 43, 0.89);
}

.comment-text p {
    margin: 0;
}

.comment-container {
    margin-left: 15px;
}
